$backgroundColor: var(--calendar-bg);

//$buttonBoxShadowActive: var(--buttonBoxShadowActive-box-shadow);
:root {
  --fc-button-text-color: var(--accent-text-color);
  --fc-button-bg-color: var(--base1);
  --fc-button-hover-bg-color: var(--base1);
  --fc-button-active-bg-color: var(--base1);

  --fc-event-bg-color: var(--calendar-fc-event-bg-color);
  --fc-event-text-color: var(--calendar-fc-event-text-color);

  --fc-border-color:var(--primary-text-color);

  --fc-event-resizer-thickness: 8px;
  --fc-event-resizer-dot-total-width: 8px;
  --fc-event-resizer-dot-border-width: 1px;

  --fc-highlight-color: var(--calendar-fc-highlight-color);
  --fc-today-bg-color: transparent;

  --fc-daygrid-event-dot-width: 11px;
  --fc-list-event-hover-bg-color: var(--calendar-fc-list-event-hover-bg-color);
}
:global(.fc-theme-standard .fc-list-day-cushion){
  background: var(--base3);
}


/////////// HEADER 2 BUTTTONS PREVIOUS AND NEXT   //////////////////////////////
:global(.fc) {
  font-family: var(--primary-font-family);
  color: var(--label);
}
:global(.fc-prev-button.fc-button.fc-button-primary),
:global(.fc-next-button.fc-button.fc-button-primary) {
  border: none;
  border-radius: 10px;
  width: 40px;
  height: 35px;
  color: var(--accent-color);
  background: var(--base1);
  box-shadow: var(--buttonBoxShadow-box-shadow);

  & span {
    position: relative;
    bottom: 3px;
    right: 4px;
    font-size: 1.8em;
  }
  &:active {
    width: 40px;
    height: 35px;
    color: var(--primary-text-color);
    box-shadow: var(--buttonBoxShadowActive-box-shadow) !important;
    background: var(--base1);

    & span {
      font-size: 1.75em;
    }
  }
  &:not(:first-child) {
    margin-left: 5px;
  }
}
/////////// HEADER BUTTTONS TODAY  //////////////////////////////
:global(.fc-today-button.fc-button.fc-button-primary) {
  border: none;
  border-radius: 15px;
  padding: 6px 25px;
  font-size: 16px;
  font-weight: 500;
  color: var(--accent-color);
  background: var(--base1);
  box-shadow: var(--buttonBoxShadow-box-shadow);

  @media (max-width: 1060px) {
    margin-left: 0 !important;
  }
  &:focus {
    color: var(--primary-text-color);
    box-shadow: var(--buttonBoxShadowActive-box-shadow) !important;
    background: var(--base4);
  }
  &:disabled {
    color: var(--primary-text-color);
    box-shadow: none;
    //background: var(--base4);
  }
}

/////////// HEADER 4 BUTTTONS RIGHT   //////////////////////////////
:global(.fc-dayGridMonth-button.fc-button.fc-button-primary),
:global(.fc-timeGridWeek-button.fc-button.fc-button-primary),
:global(.fc-timeGridDay-button.fc-button.fc-button-primary),
:global(.fc-listWeek-button.fc-button.fc-button-primary) {
  font-weight: 500;
  font-size: 15px;
  border-radius: 15px;
  border: none;
  padding: 6px 25px;
  color: var(--accent-color);
  background: var(--base1);
  box-shadow: var(--buttonBoxShadow-box-shadow) !important;
  margin-left: 0;
  margin-right: 12px;
  &:focus {
    background: var(--base4);
    color: var(--additionalLighterColor);
    box-shadow: var(--buttonBoxShadowActive-box-shadow);
  }
}
:global(.fc-listWeek-button.fc-button.fc-button-primary) {
  margin-right: 20px;
}


:global(.fc .fc-timegrid-axis-frame){
  padding-left: 30px!important;
}

:global(.fc .fc-toolbar.fc-header-toolbar){
  display: flex!important;
flex-wrap: wrap;
  align-items: center!important;
}

:global(.fc .fc-toolbar.fc-header-toolbar div){
  padding: 10px;
  box-sizing: border-box;
}

:global(.fc .fc-timegrid-event){
  //box-shadow: var(--outset-box-shadow)!important;
  box-shadow: none!important;
  //border: 2px solid var(--dark-text-color) !important;
  padding: 0!important;
}

:global(.fc .fc-timegrid-event:nth-child(2n+1)){
  border: 3px solid black !important;
}


:global(.fc-daygrid-event-dot){
  //border: none !important;
  background: var(--accent-color)!important;
}

:global(col){
  width: 70px!important;
  margin-left: 10px!important;
}



:global(thead .fc-scrollgrid-sync-inner ){

}

:global(.css-14s5rfu-MuiFormLabel-root-MuiInputLabel-root.Mui-error){
  color: var(--errorColor)!important;
}

:global(.MuiFormHelperText-root.Mui-error){
color: var(--errorColor)!important;
}

:global(.css-1sumxir-MuiFormLabel-root-MuiInputLabel-root.Mui-focused){
  color: var(--additionalColor)!important;
  font-size: 20px!important;
  top:-10px!important;
}

:global(.css-1sumxir-MuiFormLabel-root-MuiInputLabel-root){
  color: var(--label) !important;
  top:-10px!important;
  font-size: 20px!important;
}

:global(.css-hfutr2-MuiSvgIcon-root-MuiSelect-icon){
  color: var(--label)!important;
}

:global(.css-bpeome-MuiSvgIcon-root-MuiSelect-icon){
  color: var(--label)!important;
}

:global(.fc-timegrid-event .fc-event-time ){
  width: 100% !important;
  font-size: 14px!important;
  color: var(--base1)!important;
  //background: var(--colorNavTitle1_2);
  //background: var(--colorNavTitle3_3);
  border-radius: 10px 10px 0 0;
}

:global(.fc-v-event .fc-event-title-container ){
  font-size: 12px!important;
  color: var(--base1) !important;
  font-weight: 500!important;
  padding-left: 10px;
  background: var(--colorNavTitle1_3);
  border-radius: 0px 0px 10px 10px;
}

:global(.fc .fc-scroller:nth-child(1) ){
  overflow: auto!important;
}

:global(.fc .fc-scrollgrid-section-header > *, .fc .fc-scrollgrid-section-footer > *){
  overflow: hidden!important;
}

