/////////// HEADER 4 BUTTTONS RIGHT ACTIVE CLASS   //////////////////////////////
:global(.fc-dayGridMonth-button.fc-button.fc-button-primary.fc-button-active),
:global(.fc-timeGridWeek-button.fc-button.fc-button-primary.fc-button-active),
:global(.fc-timeGridDay-button.fc-button.fc-button-primary.fc-button-active),
:global(.fc-listWeek-button.fc-button.fc-button-primary.fc-button-active) {
box-shadow: var(--inset-box-shadow) !important;
color: var(--primary-text-color) !important;
background: var(--base1);
cursor: default;
}

/////////// HEADER Language BUTTTONS   //////////////////////////////
:global(.fc-buttonUa-button.fc-button.fc-button-primary),
:global(.fc-buttonEn-button.fc-button.fc-button-primary) {
border: none;
font-weight: 500;
font-size: 15px;
border-radius: 2px;
background: var(--base1);
color: var(--accent-color);
box-shadow: var(--buttonBoxShadow-box-shadow);
&:focus {
color: var(--primary-text-color);
background: var(--base1);
box-shadow: var(--inset-box-shadow) !important;
}
}
:global(.fc-buttonUa-button.fc-button.fc-button-primary.active),
:global(.fc-buttonEn-button.fc-button.fc-button-primary.active) {
color: var(--primary-text-color);
box-shadow: var(--HEADERLanguageBUTTTONS-box-shadow);
cursor: default;
}

///////////  SCREEN CALENDAR   //////////////////////////////
:global(.fc.fc-media-screen.fc-direction-ltr.fc-theme-standard) {
padding-top: 10px;
background-color: $backgroundColor;
border-radius: 10px;
}

/////////// HEADER TOOLBAR   //////////////////////////////
:global(.fc-header-toolbar.fc-toolbar.fc-toolbar-ltr) {
padding: 0 10px;
}

//////// NAME OF WEEKS   //////////
:global(.fc-col-header-cell-cushion) {
text-transform: uppercase;
}
//////// NUMBER OF DAY TOP AND WEEK  //////////
:global(a.fc-daygrid-week-number) {
margin-top: 9px;
margin-left: 4px;
border-radius: 8px;
box-shadow: var(--NUMBEROFDAYTOP-box-shadow);
padding: 1px 7px !important;
font-size: 18px;
background-color: transparent !important;
}
:global(.fc-daygrid-day-top) {
margin-top: 9px;
margin-right: 11px;
& a {
border-radius: 8px;
box-shadow: var(--NUMBEROFDAYTOP-box-shadow);
padding: 1px 7px !important;
font-size: 18px;
font-weight: 500;
}
}

////////////EVENT IN DAY////////////////////////
:global(.fc-event-main) {
overflow: hidden;
}

//////Border for Table inside body month////////////////////
:global(.fc-daygrid-day.fc-day) {
border: none;
padding: 3px;
}
:global(.fc-day-today .fc-daygrid-day-frame) {
box-shadow: var(--BorderForTableInsideBodyMonth-box-shadow);
background-color: var(--primary-color);
position: relative;
z-index: 1;
}
:global(.fc-daygrid-day-frame) {
//box-shadow: inset 18px 18px 20px #d1d9e6, inset -18px -18px 20px #ffffff;
border-radius: 10px;
background-color: var(--primary-color);
}

:global(.fc-highlight) {
border-radius: 10px;
}

//////Box Shadow for Table Top month////////////////////
:global(thead .fc-scrollgrid-sync-inner) {
// box-shadow: inset 5px 3px 15px #d1d9e6, inset -4px -3px 7px #ffffff;
border-radius: 18px;
margin: 8px 4px 10px 4px;
text-align: center;
background-color: var(--primary-color);
& a {
font-size: 14px;
}
}

////////Remove All Border Table around Calendar///////////////
:global(.fc-theme-standard th),
:global(.fc-dayGridMonth-view td[role="presentation"]),
:global(.fc-theme-standard .fc-scrollgrid) {
border: none;
}

:global(.fc-timeGridDay-view tbody),
:global(.fc-listWeek-view),
:global(.fc-timeGridWeek-view td[role="presentation"]) {
background-color: var(--primary-color);
}

:global(.fc-list-event):hover {
background: var(--calendar-fc-list-event-hover-bg-color);
}

:global(td .fc-timegrid-col) {
border: 1px solid var(--fc-border-color);
}

//// Event Highlight ////////////
:global(.fc-timegrid-event) {
box-shadow: inset 1px 2px 6px 0px #ffffff, inset -1px -3px 6px 1px #616161 !important;
border: none;
border-radius: 10px;
padding: 1px 8px;
font-size: 12px;
}

:global(.fc-daygrid-day-events) {
padding-right: 1px;
padding-left: 3px;
}
:global(.fc-event-time) {
font-weight: 600;
text-align: center;
}
:global(.fc-more-popover) {
z-index: 3 !important;
&.fc-popover-body {
max-height: 110px;
overflow: auto;
}
}
:global(.fc-popover-body) {
max-height: 110px;
overflow: auto;
}

///////////////////// Title ////////////////////////
:global(.fc-toolbar-title) {
color: var(--accent-text-color);
@media (max-width: 1320px) {
font-size: 18px !important;
}
}

:global(.fc-button) {
@media (max-width: 1060px) {
margin-bottom: 10px !important;
}
}
